import React from "react";
import styled from "styled-components";
import { withRouter, Link } from "react-router-dom";

import { primaryColor, headline, paragraph, link as linkColor } from "./Colors";

import background from "./images/background.svg";
import cloudLeft from "./images/cloud1.svg";
import cloudRight from "./images/cloud2.svg";

const BackgroundContainer = styled.div`
  width: 100%;
  height: 100%;
`;

const AboutUs = styled.section`
  height: 50vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  background: url(${background}) no-repeat bottom, url(${cloudLeft}) no-repeat,
    url(${cloudRight}) no-repeat;
  background-size: auto, contain, contain;
  animation: 5s ease-in-out infinite alternate moveLeftCloud;
  @keyframes moveLeftCloud {
    from {
      background-position: bottom, top 20px left 0, top 20px right;
    }
    to {
      background-position: bottom, top 40px left 100px, top 20px right 50px;
    }
  }

  & p {
    margin: 0.5em;
    font-weight: bold;
    color: ${headline};

    @media screen and (min-width: 600px) {
      font-size: 1.2em;
    }
  }
  & p:first-child {
    font-size: 2em;
    color: ${paragraph};

    @media screen and (min-width: 600px) {
      font-size: 2.5em;
    }
  }
`;

const ActionButton = styled.button`
  padding: 1em;
  font-weight: bold;
  box-shadow: none;
  color: ${linkColor};
  background: white;
  transition: all 0.5s ease-in-out;
  border: 0.5px solid ${primaryColor};
  border-radius: 5px;

  &:hover {
    color: ${primaryColor};
    background: white;
    font-weight: bolder;
  }
`;

const HowItWorks = styled.section`
  background: #f6efef;
  text-align: center;

  & h2 {
    margin: 0;
    padding: 0.83em;
  }
`;

const HowContainer = styled.div`
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  @media screen and (min-width: 768px) {
    max-width: 80%;
    margin: auto;
  }
`;

const How = styled.section`
  border: solid #9e9191 1px;
  border-radius: 10px;
  width: 85%;
  padding: 1em;
  margin-bottom: 1em;
  color: white;

  & p:first-of-type {
    font-size: 1.5em;
    font-weight: bold;
  }

  @media screen and (min-width: 600px) {
    width: 45%;
  }
  @media screen and (min-width: 992px) {
    width: 30%;
  }
`;

const ContactUs = styled.section`
  background-color: ${linkColor};
  width: 100%;
  padding: 1em;
  color: #fffffe;
`;

const FormContainer = styled.form`
  width: 80%;
  margin: auto;
  justify-content: center;
`;

const InputLabel = styled.label`
  display: block;
  padding: 0.5em;
`;

const ShortInput = styled.input`
  width: 100%;
  outline: 0;
  box-shadow: none;
  padding: 0.83em;
  border-radius: 5px;

  @media screen and (min-width: 768px) {
    width: 40%;
  }
`;
const LongInput = styled.textarea`
  width: 100%;
  padding: 0.83em;
`;

const Landing = ({ history }) => {
  const onAction = () => {
    history.push("/rent");
  };
  return (
    <BackgroundContainer>
      <AboutUs>
        <p>Find House Effortlessly</p>
        <p>
          Discover suitable houses to rent without leaving the comfort of you
          house
        </p>
        <ActionButton type="button" onClick={onAction}>
          <Link to="/rent">Find Houses Now!</Link>
        </ActionButton>
      </AboutUs>
      <HowItWorks>
        <h2>How it works</h2>
        <HowContainer>
          <How style={{ backgroundColor: "#073b4cff" }}>
            <i className="fas fa-file-signature fa-3x" />
            <p>Describe your interest</p>
            <p>Answer questions about the type of house you are looking for</p>
          </How>
          <How style={{ backgroundColor: "#ef476fff" }}>
            <i className="fab fa-think-peaks fa-3x" />
            <p>We find the best fit</p>
            <p>
              We take care of presenting you the best fit out of our catalog of
              houses
            </p>
          </How>
          <How style={{ backgroundColor: "#06d6a0ff" }}>
            <i className="fas fa-smile fa-3x" />
            <p>You select your choice</p>
            <p>
              Select from the choices and proceed with the contact information
              provided
            </p>
          </How>
        </HowContainer>
      </HowItWorks>
      <ContactUs>
        <h2>Have an enquiry? Contact us!</h2>
        <h3>We'd love to hear from you!!</h3>
        <FormContainer>
          <InputLabel htmlFor="name">Name</InputLabel>
          <ShortInput id="name" type="text" />
          <InputLabel htmlFor="phone">
            Enter your phone number
          </InputLabel>
          <ShortInput id="phone" type="phone" />
          <InputLabel htmlFor="message" type="text">
            Enter you message
          </InputLabel>
          <LongInput id="message" />
        </FormContainer>
      </ContactUs>
    </BackgroundContainer>
  );
};

export default withRouter(Landing);
