import React, { useState } from "react";
import styled from "styled-components";
import "../css/all.css";

import { withRouter } from "react-router-dom";

import {
  primaryColor,
  headline,
  paragraph,
  link as linkColor
} from "../Colors";

const NavBarContainer = styled.nav`
  font-size: 1em;
  color: white;
  @media screen and (min-width: 768px) {
    margin-top: 1em;
    display: flex;
    justify-content: space-evenly;
    background: ${primaryColor}; /* fallback for old browsers */
    background: linear-gradient(to right, #6dd5ed, #2193b0);
    padding: 10px 0;
  }
  padding: 0;
`;

const NavBarToggle = styled.i`
  cursor: pointer;
  position: absolute;
  right: 10px;
  top: 17px;
  @media screen and (min-width: 768px) {
    display: none !important;
  }
`;

const LogoContainer = styled.div`
  padding: 0.5em;
  font-size: 1.5em;
  font-weight: bold;
  color: ${linkColor};
  @media screen and (min-width: 768px) {
    background: white;
    position: absolute;
    left: 40px;
    top: 0px;
    width: 13%;
    height: auto;
    font-size: 2em;
  }

  @media screen and (min-width: 1024px) {
    width: 10%;
  }

  @media screen and (min-width: 1280px) {
    width: 10%;
  }

  @media screen and (min-width: 1368px) {
    width: 8%;
  }
`;

const Logo = styled.a`
  color: #0e24a0;
  font-family: "Dancing Script", cursive;
  text-decoration: none;
`;

const NavLink = styled.a`
  text-decoration: none;
  text-transform: uppercase;
  &:visited {
    text-decoration: none;
  }
  &:hover {
    text-decoration: none;
  }
`;

const NavLinkItem = styled.li`
  text-align: center;
  font-weight: bold;
  padding: 0.5em;
  @media screen and (min-width: 768px) {
    margin-right: 1em;
    &:last-of-type {
      margin-right: 1.5em;
    }
  }
`;

const NavLinkContainer = styled.ul`
  list-style-type: none;
  opacity: ${props => (props.open ? 0 : 1)};
  width: 100%;
  margin: 0;
  padding: 0;
  transition: opacity 0.5s;
  position: absolute;
  top: 56px;
  background: #2193b0; /* fallback for old browsers */
  background: linear-gradient(to right, #6dd5ed, #2193b0);
  @media screen and (min-width: 768px) {
    position: initial;
    opacity: 1;
    width: 90%;
    display: flex;
    justify-content: flex-end;
  }
`;

const ActionButton = styled.button`
  padding: 0.5em 1em;
  font-weight: bold;
  box-shadow: none;
  color: #ef476fff;
  background: white;
  transition: all 0.5s ease-in-out;
  border: 0.5px solid #ef476fff;
  border-radius: 5px;
  position: absolute;
  right: 40px;
  top: 15px;
  outline: none;

  &:hover {
    color: #ef476fff;
    background: white;
    font-weight: bolder;
  }

  @media screen and (min-width: 768px) {
    position: initial;
  }
`;

const NavBar = ({ history }) => {
  const [toggleState, setToggleState] = useState("close");

  const onAction = () => {
    history.push("/rent");
  };
  const handleNavBarToggle = () => {
    console.log("toggled");
    if (toggleState === "close") {
      setToggleState("open");
    } else {
      setToggleState("close");
    }
  };
  return (
    <NavBarContainer>
      <LogoContainer>
        <Logo href="/housera">Housera</Logo>
        <NavBarToggle
          onClick={handleNavBarToggle}
          className={`fas ${
            toggleState === "close" ? "fa-bars" : "fa-window-close"
          }`}
        />
      </LogoContainer>
      <NavLinkContainer open={toggleState === "close"}>
        <NavLinkItem href="#">
          <NavLink>About</NavLink>
        </NavLinkItem>
        <NavLinkItem href="#">
          <NavLink>Services</NavLink>
        </NavLinkItem>
        <NavLinkItem href="#">
          <NavLink>Contact Us</NavLink>
        </NavLinkItem>
      </NavLinkContainer>
      <ActionButton type="button" onClick={onAction}>
        Rent Now!
      </ActionButton>
    </NavBarContainer>
  );
};

export default withRouter(NavBar);
